import * as d3 from "d3";
function Broken_line_chart(data) {
    // 定义数据集
    var data = [
        { year: "2015", Total_Energy_Consumptio: 434113, Coal: 62.8, Petroleum: 18.4, Natural_Gas: 5.8, Primary_Electricity_and_Other_Energy: 12.0 },
        { year: "2016", Total_Energy_Consumptio: 441492, Coal: 62.2, Petroleum: 18.7, Natural_Gas: 6.1, Primary_Electricity_and_Other_Energy: 13.0 },
        { year: "2017", Total_Energy_Consumptio: 455827, Coal: 60.6, Petroleum: 18.9, Natural_Gas: 6.9, Primary_Electricity_and_Other_Energy: 13.6 },
        { year: "2018", Total_Energy_Consumptio: 471925, Coal: 59.0, Petroleum: 18.9, Natural_Gas: 7.6, Primary_Electricity_and_Other_Energy: 14.5 },
        { year: "2019", Total_Energy_Consumptio: 487488, Coal: 57.7, Petroleum: 19.0, Natural_Gas: 8.0, Primary_Electricity_and_Other_Energy: 15.3 },
        { year: "2020", Total_Energy_Consumptio: 498314, Coal: 56.9, Petroleum: 18.8, Natural_Gas: 8.4, Primary_Electricity_and_Other_Energy: 15.9 },
        { year: "2021", Total_Energy_Consumptio: 525896, Coal: 55.9, Petroleum: 18.6, Natural_Gas: 8.8, Primary_Electricity_and_Other_Energy: 16.7 }
    ];

    // 设置SVG的宽度和高度
    var width = 1000;
    var height = 500;
    var margin = { top: 40, right: 50, bottom: 60, left: 50 };
    var innerWidth = width - margin.left - margin.right;
    var innerHeight = height - margin.top - margin.bottom;
    // 创建一个div元素作为SVG容器
    var container = d3.select("body")
        .append("div")
        .attr("class", "chart-container");

    // 添加标题
    container.append("h2")
        .text("能源消费总量及构成");

    // 创建SVG
    var svg = container.append("svg")
        .attr("width", width)
        .attr("height", height);

    // 创建一个内部容器，并添加到SVG中，作为绘制图形的区域
    var g = svg.append("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    // 创建一个比例尺，将年份映射到X轴上的位置
    var xScale = d3.scaleBand()
        .domain(data.map(d => d.year))
        .range([0, innerWidth])
        .padding(0.1);

    // 创建一个比例尺，将资源消耗占比映射到Y1轴上的位置
    var y1Scale = d3.scaleLinear()
        .domain([0, d3.max(data, d => Math.max(d.Coal, d.Petroleum, d.Natural_Gas, d.Primary_Electricity_and_Other_Energy))])
        .range([innerHeight, 0]);

    // 创建一个比例尺，将资源消耗占比映射到Y2轴上的位置
    var y2Scale = d3.scaleLinear()
        .domain([0, d3.max(data, d => Math.max(d.Total_Energy_Consumptio))])
        .range([innerHeight, 0]);

    // 创建一个X轴、Y1轴、Y2轴生成器，用于在SVG中添加这些轴
    var xAxis = d3.axisBottom(xScale);
    var y1Axis = d3.axisLeft(y1Scale);
    var y2Axis = d3.axisRight(y2Scale);

    // 在SVG中添加X轴
    g.append("g")
        .attr("class", "x-axis")
        .attr("transform", "translate(0," + innerHeight + ")")
        .call(xAxis);

    // 在SVG中添加Y1轴
    g.append("g")
        .attr("class", "y1-axis")
        .call(y1Axis);

    // 在SVG中添加Y2轴
    g.append("g")
        .attr("class", "y2-axis")
        .attr("transform", "translate(" + innerWidth + ",0)")
        .call(y2Axis);

    //添加柱状图
    g.selectAll(".bar")
        .data(data)
        .enter().append("rect")
        .attr("class", "bar")
        .attr("x", function (d) { return xScale(d.year); })
        .attr("width", xScale.bandwidth())
        .attr("y", function (d) { return y2Scale(d.Total_Energy_Consumptio); })
        .attr("height", function (d) { return innerHeight - y2Scale(d.Total_Energy_Consumptio); })
        .attr("fill", "#5E92CC");

    // 创建煤炭曲线
    var line1 = d3.line()
        .x(d => xScale(d.year) + xScale.bandwidth() / 2)
        .y(d => y1Scale(d.Coal))
        .curve(d3.curveBumpX);

    // 创建石油曲线
    var line2 = d3.line()
        .x(d => xScale(d.year) + xScale.bandwidth() / 2)
        .y(d => y1Scale(d.Petroleum))
        .curve(d3.curveBumpX);

    // 创建天然气曲线
    var line3 = d3.line()
        .x(d => xScale(d.year) + xScale.bandwidth() / 2)
        .y(d => y1Scale(d.Natural_Gas))
        .curve(d3.curveBumpX);

    // 创建一次电力及其他能源曲线
    var line4 = d3.line()
        .x(d => xScale(d.year) + xScale.bandwidth() / 2)
        .y(d => y1Scale(d.Primary_Electricity_and_Other_Energy))
        .curve(d3.curveBumpX);

    // 在SVG中绘制煤炭曲线
    g.append("path")
        .datum(data)
        .attr("fill", "none")
        .attr("stroke", "red")
        .attr("stroke-width", 2.5)
        .attr("d", line1)
        .style("stroke-linejoin", "round") // 设置线段连接处为圆角
        .style("stroke-linecap", "round") // 设置线段末端为圆角
        .transition()
        .duration(1000)
        .attrTween("stroke-dasharray", function () {
            var length = this.getTotalLength();
            return function (t) {
                return d3.interpolateString("0," + length, length + "," + length)(t);
            };
        });

    // 在SVG中绘制石油曲线
    g.append("path")
        .datum(data)
        .attr("fill", "none")
        .attr("stroke", "blue")
        .attr("stroke-width", 2.5)
        .attr("d", line2)
        .style("stroke-linejoin", "round") // 设置线段连接处为圆角
        .style("stroke-linecap", "round") // 设置线段末端为圆角
        .transition()
        .duration(1000)
        .attrTween("stroke-dasharray", function () {
            var length = this.getTotalLength();
            return function (t) {
                return d3.interpolateString("0," + length, length + "," + length)(t);
            };
        });

    // 在SVG中绘制天然气曲线
    g.append("path")
        .datum(data)
        .attr("fill", "none")
        .attr("stroke", "orange")
        .attr("stroke-width", 2.5)
        .attr("d", line3)
        .style("stroke-linejoin", "round") // 设置线段连接处为圆角
        .style("stroke-linecap", "round") // 设置线段末端为圆角
        .transition()
        .duration(1000)
        .attrTween("stroke-dasharray", function () {
            var length = this.getTotalLength();
            return function (t) {
                return d3.interpolateString("0," + length, length + "," + length)(t);
            };
        });

    // 在SVG中绘制一次电力及其他能源曲线
    g.append("path")
        .datum(data)
        .attr("fill", "none")
        .attr("stroke", "green")
        .attr("stroke-width", 2.5)
        .attr("d", line4)
        .style("stroke-linejoin", "round") // 设置线段连接处为圆角
        .style("stroke-linecap", "round") // 设置线段末端为圆角
        .transition()
        .duration(1000)
        .attrTween("stroke-dasharray", function () {
            var length = this.getTotalLength();
            return function (t) {
                return d3.interpolateString("0," + length, length + "," + length)(t);
            };
        });

    // 绘制煤炭数据点
    g.selectAll(".data-point-Coal")
        .data(data)
        .enter()
        .append("circle")
        .attr("class", "data-point-Coal")
        .attr("cx", d => xScale(d.year) + xScale.bandwidth() / 2)
        .attr("cy", d => y1Scale(d.Coal))
        .attr("r", 0)
        .style("fill", "red") // 设置填充颜色为红色
        .on("mouseover", function () {
            d3.select(this)
                .transition()
                .attr("r", 6);
        })
        .on("mouseout", function () {
            d3.select(this)
                .transition()
                .attr("r", 4);
        })
        .transition()
        .delay(1000)
        .duration(500)
        .attr("r", 4);

    // 绘制石油数据点
    g.selectAll(".data-point-Petroleum")
        .data(data)
        .enter()
        .append("circle")
        .attr("class", "data-point-Petroleum")
        .attr("cx", d => xScale(d.year) + xScale.bandwidth() / 2)
        .attr("cy", d => y1Scale(d.Petroleum))
        .attr("r", 0)
        .style("fill", "blue") // 设置填充颜色为蓝色
        .on("mouseover", function () {
            d3.select(this)
                .transition()
                .attr("r", 6);
        })
        .on("mouseout", function () {
            d3.select(this)
                .transition()
                .attr("r", 4);
        })
        .transition()
        .delay(1000)
        .duration(500)
        .attr("r", 4);

    // 绘制天然气数据点
    g.selectAll(".data-point-Natural_Gas")
        .data(data)
        .enter()
        .append("circle")
        .attr("class", "data-point-Natural_Gas")
        .attr("cx", d => xScale(d.year) + xScale.bandwidth() / 2)
        .attr("cy", d => y1Scale(d.Natural_Gas))
        .attr("r", 0)
        .style("fill", "orange") // 设置填充颜色为橙色
        .on("mouseover", function () {
            d3.select(this)
                .transition()
                .attr("r", 6);
        })
        .on("mouseout", function () {
            d3.select(this)
                .transition()
                .attr("r", 4);
        })
        .transition()
        .delay(1000)
        .duration(500)
        .attr("r", 4);

    // 绘制一次电力及其他能源数据点
    g.selectAll(".data-point-Primary_Electricity_and_Other_Energy")
        .data(data)
        .enter()
        .append("circle")
        .attr("class", "data-point-Primary_Electricity_and_Other_Energy")
        .attr("cx", d => xScale(d.year) + xScale.bandwidth() / 2)
        .attr("cy", d => y1Scale(d.Primary_Electricity_and_Other_Energy))
        .attr("r", 0)
        .style("fill", "green") // 设置填充颜色为绿色
        .on("mouseover", function () {
            d3.select(this)
                .transition()
                .attr("r", 6);
        })
        .on("mouseout", function () {
            d3.select(this)
                .transition()
                .attr("r", 4);
        })
        .transition()
        .delay(1000)
        .duration(500)
        .attr("r", 4);

    // 添加煤炭标签
    g.selectAll(".data-label-Coal")
        .data(data)
        .enter()
        .append("text")
        .attr("class", "data-label-Coal")
        .attr("x", d => xScale(d.year) + xScale.bandwidth() / 2)
        .attr("y", d => y1Scale(d.Coal) - 10)
        .text(d => d.Coal)
        .style("text-anchor", "middle")
        .style("opacity", 0)
        .transition()
        .delay(1000)
        .duration(500)
        .style("opacity", 1);

    // 添加石油标签
    g.selectAll(".data-label-Petroleum")
        .data(data)
        .enter()
        .append("text")
        .attr("class", "data-label-Petroleum")
        .attr("x", d => xScale(d.year) + xScale.bandwidth() / 2)
        .attr("y", d => y1Scale(d.Petroleum) + 15)
        .text(d => d.Petroleum)
        .style("text-anchor", "middle")
        .style("opacity", 0)
        .transition()
        .delay(1000)
        .duration(500)
        .style("opacity", 1);

    // 添加天然气标签
    g.selectAll(".data-label-Natural_Gas")
        .data(data)
        .enter()
        .append("text")
        .attr("class", "data-label-Natural_Gas")
        .attr("x", d => xScale(d.year) + xScale.bandwidth() / 2)
        .attr("y", d => y1Scale(d.Natural_Gas) - 10)
        .text(d => d.Natural_Gas)
        .style("text-anchor", "middle")
        .style("opacity", 0)
        .transition()
        .delay(1000)
        .duration(500)
        .style("opacity", 1);

    // 添加一次电力及其他能源标签
    g.selectAll(".data-label-Primary_Electricity_and_Other_Energy")
        .data(data)
        .enter()
        .append("text")
        .attr("class", "data-label-Primary_Electricity_and_Other_Energy")
        .attr("x", d => xScale(d.year) + xScale.bandwidth() / 2)
        .attr("y", d => y1Scale(d.Primary_Electricity_and_Other_Energy) + 15)
        .text(d => d.Primary_Electricity_and_Other_Energy)
        .style("text-anchor", "middle")
        .style("opacity", 0)
        .transition()
        .delay(1000)
        .duration(500)
        .style("opacity", 1);

    // 添加数据点和标签的鼠标交互
    g.selectAll(".data-point-Coal, .data-point-Petroleum")
        .on("mouseover", function () {
            d3.select(this)
                .transition()
                .attr("r", 6);
        })
        .on("mouseout", function () {
            d3.select(this)
                .transition()
                .attr("r", 4);
        });

}

export default Broken_line_chart;
Broken_line_chart();